html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #fafafa;
    font-size: 15px;
    line-height: 1.555;
    font-weight: 400;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #333;
    -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
}

* {
    outline: none;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

::selection {
    background-color: rgba(0, 0, 0, 0.12);
}

img {
    max-width: 100%;
}

pre {
    white-space: pre-wrap;
    word-wrap: break-word;
}

#app {
    max-width: 640px;
    margin: auto;
    /* box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.12); */
    background-color: #f4f4f4;
}

.swiper-wrapper, .swiper-slide, .swiper-container {
    width: 100% !important;
}

.swiper-slide {
    background-color: #fafafa;
    height: 100%;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.08) inset;
}

.swiper-pagination-bullet {
    box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.12);
}

.swiper-pagination-bullet-active {
    background-color: rgba(255, 30, 30, 0.48);
}

.swiper-pagination {
    bottom: 6px !important;
}

.gradient {
    background: linear-gradient(to top right, #f44, #f77);
    background: -webkit-gradient(linear, left bottom, right top, from(#f44), to(#f77));
    /* background: -webkit-gradient(linear, left bottom, right top, from(#37f), to(#3cf)); */
    /* background: -webkit-gradient(linear, left bottom, right top, from(#76f), to(#aaf)); */
}

.gradient-dark {
    background: linear-gradient(to top right, #f33, #f55);
    background: -webkit-gradient(linear, left bottom, right top, from(#f33), to(#f55));
}

.space {
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}

.onePx-line::before {
    content: '';
    pointer-events: none;
    position: absolute;
    width: 200%;
    height: 200%;
    left: 0; top: 0;
    border-bottom: 0.5px solid #eee;
    transform: scale(0.5);
    transform-origin: 0 0;
}

@keyframes music-thumb {
    0% {
        transform: rotate(0deg);
    } 100% {
        transform: rotate(360deg);
    }
}

@keyframes loading-run {
    0% {
        opacity: 1;
    } 100% {
        opacity: 0;
    }
}




